.bkground-icon1 {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  object-fit: cover;
}
.schoolpage {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-27xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-304xl);
  letter-spacing: normal;
}
@media screen and (max-width: 725px) {
  .schoolpage {
    gap: var(--gap-304xl);
  }
}
@media screen and (max-width: 450px) {
  .schoolpage {
    gap: var(--gap-304xl);
  }
}
